.productList, .productDisplay{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
}

.productList .productDisplay{
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: row;
            flex-direction: row;
    width: 90%;
    margin: 0 auto;
}

img{
    width: 10em;
    height: auto;
}

.cart{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.cartList{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.product{
    border: black solid 3px;
    margin: 1em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 15em;
    height: 15em;
    border-radius: 10%;
}

.product:hover{
    border-color: #dadfea;
}

.product h3 {
    text-align: center;
    margin: 0;
    padding: 0;
}

.product p{
    margin: 0;
    padding: 0;
}

.product img{
    /*max-height: 10em;*/
    width: auto;
    height: 7em;
    margin-top: .25em;
    /*max-width: 15em;*/
}

.cartItemControls{
    border: black solid 2px;
    margin: .5em;
    padding: .25em;
}
.cartItemControls input{
    width: 25%;
}

.cartProduct{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    border: black solid 3px;
    height: 20em;
    width: 20em;
    margin: 1em;
    text-align: center;
}

.cartProduct img{
    height: 10em;
    width: auto;
    margin-top: .25em;
}

.cartProduct h3, .cartProduct p{
    margin: 0;
    padding: 0;
}

#prodDisplayImg{
    width: auto;
    height: 20em;
}

.productDisplay h1, .productDisplay h3{
    margin: 0;
    padding: 0;
    text-align: center;
}

.controls{
    margin-top: .5em;
}

